#tag-selector, .tag-selector {
    margin: 2rem 0;
    text-align: center;

    #feeling-lucky, .tag-button {
        border: 1px solid black;
        background-color: $content_color;
        display: inline-block;
        padding: 0.2rem 1rem;
        font-family: $mono_fonts;
        font-size: .8rem;
        box-shadow: 5px 5px 0 black;
        margin: 5px;
        margin-left: 0;
        cursor: pointer;
        
        &:hover {
            color: #000;
        }
        
        &:active {
            position: relative;
            top: 5px;
            left: 5px;
            box-shadow: none;
        }
        
        &.active-filter {
            position: relative;
            top: 5px;
            left: 5px;
            box-shadow: none;
            background-color: #ddd;
            color: #000;
        }
        
        &#tag-tutti {
            font-weight: bold;
        }
    }

    #feeling-lucky {
        background-color: #c40000;
        color: #fff;
        font-weight: bold;
    }
}

.manuale {
    #cork-board {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        flex-wrap: wrap;
        align-items: flex-start;
        margin: 0 auto;

        .quote {
            background-color: $content_color;
            box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
            margin-bottom: 1rem;
            padding: 1rem;
            width: 16rem;
            
            p {
                margin-top: 0;
            }
            
            .who {
                text-align: right;
                font-size: .8rem;
            }
            
            .link {
                float: left;
                font-size: 0.6rem;
                position: relative;
                top: 0.4rem;
                display: none;
            }

            &:hover .link {
                display: block;
            }

            footer {
                font-size: .8rem;
                display: none;
            }
        }
    }
}

article.manuale {
    background-color: $content_color;
    border: 1px solid black;
    padding: 1rem;
    box-shadow: 2px 2px 0px #ddd;
    
    p {
        margin-top: 0;
    }
    
    .who {
        text-align: right;
        font-size: .8rem;
    }

    #meta, h1 {
        display: none;
    }
}